element.style {
  background-color: rgb(42, 42, 255);
}

.size{
  height: auto;
  font-size: 17px;
  display: flex;
  text-align: center;
  flex-wrap: wrap;
  justify-content: space-evenly;
}


.wrapper button {
  border: 0;
  background: #bcbcbc;
  cursor: pointer;
  border-radius: 9px;
  }

ul {
  display: flex;
  padding: 0px;
}

li {
  margin: 10 20px;
  list-style: none;
  padding: 0;
}



.logOut {
  margin-left: 10px;
}

.topRow1 {
  margin: 5px;
}

.topRow1:active {
  margin: 5px;
  background: #dfdfdf;
  color:rgb(111, 111, 111);
}

.topRow2 {
  display: flexbox;
  margin: 1vw;
  background: #f3f3f3;
  cursor: pointer;
  border-radius: 9px;
  padding-bottom: 11px;
  padding-top: 12px;
  padding-left: 1vw;
  padding-right: 1vw;
  text-decoration: inherit;
  color: rgb(102, 102, 102);
}

#journeySubmitButton {
  margin-bottom: 10px;
