  background: #7f7f7f;
  cursor: pointer;
  border-radius: 9px;
  padding-bottom: 5px;
  padding-top: 5px;
  padding-left: 12px;
  padding-right: 12px;
  text-decoration: none;
  color: white;
  font-size: 12px;
}

#useTrains{
  margin-left: 2px;
