#useCurrentLocation:active{
  background: #59a7ff;
}

#useTrains:active{
  background: #59a7ff;
}


#manualSubmitButton:active{
  background: #59a7ff;
}


#journeySubmitButton:active{
  background: #59a7ff;
}

button#showHide:active {
  color: white;
  background: #59a7ff;
}

.topRow2:active {
  background: #bfbfbf;
  color:black;
}

a.topRow2.active{
  background: #0080ff;
  color: rgb(255, 255, 255);
}

navbar.NavBar1{
  padding-bottom: 0.85pc;
  padding-top: 0.9pc;
  border-radius: 9px;
}

.backHome{
  margin: 0;
  background: #efefef;
  cursor: pointer;
  border-radius: 9px;
  padding-left: 10px;
  padding-right: 10px;
  text-decoration: inherit;
}

.transactions {
  display: inline-table;
  width: 98vw;
  text-align: center;
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  font-size: 15px;
  border-radius: 20px;
  color: black;
  background-color: #f0f0f0;
  }

.transactions td{
  border: 0px solid #ddd;
  border-radius: 0px;
  padding: 6px;  

}

.transactions tr:hover {
  background-color: #c2c2c2;
  border-radius: 20px;

}

.TableRow{
  text-align: left;
}

.highlights{
  font-weight:600;
  background: #ffaeae;
  border-radius: 20px;
  padding: 10px;
  text-align: center;
  color: black;
  font-size: 15px;
  margin-bottom: 0px;
}

