  background: #6bab9d;
  border-radius: 20px;
  padding: 10px;
  margin-right: 8px;
  margin-left: 8px;

  display: flex;
  text-align: center;
  color: black;
}

div.gm-style {
  overflow: hidden;
  white-space: nowrap;
